<template>
  <div :class="[className,{hoverBlock:hover}]" @mouseover="isHover" @mouseleave="isNotHover">

  <header class="clear-fix">
            <span class="fa fa-briefcase"></span>
            <span>
                <h2>校内实践</h2>
            </span>
        </header>
        <ul class="intro-content-1">
          <content-o-r-input
            v-for="(content,index) in contents"
            :model="'contentModelOne'"
            :tittle1="content.tittle1"
            :tittle2="content.tittle2"
            :tittle3="content.tittle3"
            :content="content.content"
            :index="index"
            :key="index"
            :change="changeData(index)"
            :edit="edit(index)"
            :edited="edited(index)"
            :del="del(index)"
            :inputIndex="inputIndex"
          />
        </ul>
    <section class="menuBtn" v-show="hover">
      <a title="删除此版块" @click="del_">
        <section class="fa fa-minus-circle" ></section>
      </a>
      <a title="增加一条" @click="add">
        <section class="fa fa-plus-circle"></section>
      </a>
    </section>
    </div>
</template>

<script>
  import contentORInput from './ContentORInput/ContentORInput.vue'

  export default{
      name:'schoolExperience',
      props:['className','del_'],
      components:{
        contentORInput
      },
      data(){
        return{
          contents:[
                {
                  tittle1:'2017.04-2017.05',
                  tittle2:'',
                  tittle3:'',
                  content:`<p>
                        参与策划、组织浙江省第一届手势控制主题Arduino创新开发大赛。
                    </p>`
                },
                {
                  tittle1:'2014.09-2018.06',
                  tittle2:'',
                  tittle3:'',
                  content:`<p>
                        担任杭州电子科技大学计算机学院14108431班级班长一职。
                    </p>`
                },
                {
                  tittle1:'2016.09-2017.06',
                  tittle2:'',
                  tittle3:'',
                  content:`<p>
                        被聘任杭州电子科技大学计算机学院2016级助理班主任。
                    </p>`
                },
                {
                  tittle1:'2016.12',
                  tittle2:'',
                  tittle3:'',
                  content:`<p>
                        参与策划、组织第二届杭州电子科技大学软件嘉年华年终技术分享大会。
                    </p>`
                },
                {
                  tittle1:'2015.05',
                  tittle2:'',
                  tittle3:'',
                  content:`<p>
                        荣获杭州电子科技大学软件工程学院第三节PPT制作大赛三等奖。
                    </p>`
                },
                {
                  tittle1:'2014.12',
                  tittle2:'',
                  tittle3:'',
                  content:`<p>
                        荣获杭州电子科技大学软件新生班级辩论赛第二名，比赛中为一辩。
                    </p>`
                },
                {
                  tittle1:'2014.10',
                  tittle2:'',
                  tittle3:'',
                  content:`<p>
                        荣获杭州电子科技大学软件工程学院时事评论大赛最佳风采奖和三等奖。
                    </p>`
                },
                {
                  tittle1:'2014.12',
                  tittle2:'',
                  tittle3:'',
                  content:`<p>
                        荣获杭州电子科技大学软件工程学院，第一届logo设计大赛二等奖。
                    </p>`
                },

              ],
          hover:false,
          inputIndex:-1
        }
      },
    methods:{
      isHover:function(e){
        this.hover=true
      },
      isNotHover:function(e){
        this.hover=false
      },
      changeData:function(index){
        var self=this;
        return function(key,value){
          self.contents[index][key]=value;
        }
      },
      edit:function(index){
        var self=this;
        return function(){
          self.inputIndex=index;
        }
      },
      edited:function(){
        var self=this;
        return function(){
          self.inputIndex=-1;
        }
      },
      del:function(index){
        var self=this;
        return function(){
          self.inputIndex=-1;
          self.contents.splice(index,1)
        }
      },
      add:function(){
        this.contents.length<15&&this.contents.push({
          tittle1:'xxxx-xxxx',
          tittle2:'xxxx',
          tittle3:'xx',
          content:`<p>xxxx</p>`

        })
      }
    }
    }
</script>

<style></style>



